import api from '../../utils/api.js';
import util from '../../utils/util.js';
const app = getApp();
export default {
	data() {
		return {
			type: 2,
			list: [],
			data: {
				type: 2,
				action: '',
				name: '母校一角',
				images: [],
				word: {
					txt: ''
				},
				file: '',
				f_id: '',
				id: ''
			},
			maxNum: 4
		};
	},
	onLoad(options) {
		const that = this;
		// 添加还是编辑
		if (options.id) {
			api.photoDetail({
				data: {
					id: options.id
				},
				success: function(res) {
					res.action = "edit";
					res.type = that.type;
					res.word = JSON.parse(res.word);
					res.images = [];
					that.data = res;
				}
			});
		}else{
			this.data.action = "add";
		}
		// 获取模板列表
		api.keeperCover({
			data: {
				id: this.type
			},
			success: function(res) {
				that.list = res.list;
			}
		});
	},
	methods: {
		upImage() {
			const that = this;
			let num = this.maxNum - this.data.images.length;
			api.uploadImage({
				num: num,
				success:function(res){
					that.data.images = that.data.images.concat(res);
				}
			});
		},
		del(e){
			let index = e.currentTarget.dataset.index;
			this.data.images.splice(index,1);
		},
		upTemp(){
			const that = this;
			let data = JSON.parse(JSON.stringify(that.data));
			data.word = JSON.stringify(data.word);
			api.uploadImage({
				num: 1,
				success: function(result) {
					uni.uploadFile({
						url: app.globalData.host + 'common/upload',
						header: {
							'content-type': 'multipart/form-data',
							'token': app.globalData.token
						},
						filePath: result[0],
						name: 'file',
						formData: data,
						success: function(res) {
							let data = JSON.parse(res.data);
							if (data.code == 200) {
								util.success(
									'上传成功',
									function() {
										uni.redirectTo({
											url: '../albumList/albumList'
										});
									},
									1200
								);
							} else {
								util.msg(data.msg);
							}
						},
						fail: function(res) {
							console.log(res);
						}
					});
				}
			});
		},
		start() {
			if(!this.data.word.txt){
				util.msg("请输入文字内容");
				return false
			}
			if(this.data.images.length < this.maxNum){
				util.msg('还需要上传' + (this.maxNum - this.data.images.length) + '张图片');
				return false
			}
			app.globalData.templateData = this.data;
			// 开始设计
			uni.navigateTo({
				url: '../templateList/templateList?id=' + this.type
			});
		}
	},
	onUnload() {
		app.globalData.templateData = {};
	}
};
